<template>
  <div id="app" :class="{appback:current == true}">
    <van-popup v-model="ts" style="width:80%">
      <div class="tsbg" style="width:100%">
        <img style="margin-top:20px;margin-left:-30px;" src="http://hstatic.1818pay.cn/image/icon/sb-jupai@2x.png" width="100px;"
        />
        <p style="margin:20px;">{{yzs}}</p>
      </div>
    </van-popup>
    <van-popup v-model="tsok" style="width:80%">
      <div class="tsbg" style="width:100%">
        <img style="margin-top:20px;" src="http://hstatic.1818pay.cn/image/icon/_s-zhuce@2x.png" width="150px;" />
        <p style="margin:20px;">注册成功,正在前往下载页···</p>
      </div>
    </van-popup>
    <div class="app">
      <img :src="applogo" width="15%" />
      <p class="appName">{{appname}}</p>
      <p class="tex1" :class="{registerxbt:current == true}">让每个平凡的人因分享而伟大</p>
      <p class="tex2">这是一个强大的APP</p>
      <p class="tex2">邀请您免费注册体验</p>
      <p class="picc" style="margin-top: 15px;">
        <span class="span" :class="{ywinputa:current == true}">安全</span>&nbsp;&nbsp;【由<span>{{witp}}</span>承保,保证资金安全】</p>
      <p class="picc">
        <span class="span" :class="{ywinputa:current == true}">便捷</span>&nbsp;&nbsp;【芝麻认证提供数据支持】</p>
      <p class="picc">
        <span class="span" :class="{ywinputa:current == true}">全面</span>&nbsp;&nbsp;【卡医生,智能还款等】</p>
    </div>
    <div class="farm">

      <div class="phonewarp">
        <input type="number" class="phone" v-model="tel" placeholder="请输入手机号">
      </div>
      <van-row>
        <van-col span="12">
          <div class="yzmewarp">
            <input type="number" v-model="yzmno" class="yzm" placeholder="请输入验证码">
          </div>
        </van-col>
        <van-col span="2">&nbsp</van-col>

        <van-col span="10">
          <input type="button" :class="{ywcolor:current == true}" :value="fsyzm" class="yzmbtn" @click="sentyzm" :disabled='isdis'>
        </van-col>

      </van-row>
      <input type="button" class="go" :class="{ywinput:current == true}" value="马上注册体验" @click="sentget" :disabled='isgetdis'>

    </div>
    <p class="tjr">推荐人：{{tjtel | fmttel}}</p>
  </div>
</template>

<script>
import { Dialog } from "vant";
import axios from'axios'
import qs from 'qs'
export default {
  name: "app",
  data() {
    return {
      readyData:'',
      witp:'picc',
      current:false,
      applogo: "",
      appname: "",
      phone: "",
      fsyzm: "发送验证码",
      host: "",
      tjtel: "",
      tel: "",
      isdis: false,
      ts: false,
      downurl: "http://hcurrency.1818pay.cn/down/appdown.html",
      InstitutionId: "",
      appid: "",
      merChantId: "",
      yzs: "您已注册,正在前往下载页···",
      tsok: false,
      yzmno: "",
      isgetdis: false,
      vuethis: this
    };
  },
  created() {
    
    function GetQueryString(name) {
      //截取url参数
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) return unescape(r[2]);
      return null;
    }
    this.tjtel = GetQueryString("merMp");
    this.InstitutionId = GetQueryString("institutionId");
    this.appid = GetQueryString("appId");

    axios
      .get("http://hself.1818pay.cn/php/appdown.php", {
        params: {
          InstitutionId: this.InstitutionId,
          appid: this.appid
        }
      })
      .then(res => {
        this.applogo = res.data.LogoSrc;
        this.appname = res.data.Name;
        this.host = res.data.Domain;
        document.title = "欢迎注册" + res.data.Name;
      });
      if(this.InstitutionId == 'T00000008'){
        this.witp = '阳光保险'
        this.current = true
      }
  },
  filters: {
    fmttel: function(tel) {
      return tel.substr(0, 3) + "****" + tel.substr(7);
    }   //打包注释去调
  },
  methods: {
    sentyzm() {
      let self = this;
      var myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (!myreg.test(this.tel)){
        Dialog.alert({
          //可以弹出 但是取消import vue 的时候 alert为undefind
          message: "请输入正确的手机号"
        }).then(() => {
          return;
        });
        return;
      } 
      else if (this.fsyzm == "发送验证码" || this.fsyzm == "重新发送") {
        let self = this;
        // let params = 'merMp=' +  this.tel  + '&institutionId=' + this.InstitutionId + '&appId=' + this.appid
        // let postData = qs.stringify({
        //   merMp:this.tel ,
        //   institutionId:this.InstitutionId,
        //   appId:this.appid
        // })
        axios.get(this.host + "/MerChants/mergeOrInsertForHtml.shtml",{
          params:{
            merMp:this.tel ,
            institutionId:this.InstitutionId,
            appId:this.appid
          }
        })
          .then(res => {
            console.log(res)
            this.merChantId = res.data.data;

            if(res.data.respCode == "0000"){
              this.fsyzm = 60;
              this.isdis = false;
              let timer = window.setInterval(function() {
                --self.fsyzm;
                if (self.fsyzm <= 0){
                  this.isdis=false;
                  window.clearInterval(timer);
                  self.fsyzm = "重新发送";
                }
              },1000);
            } else if (
              res.data.respCode == "0001" &&
              res.data.respDesc == "此手机号已注册"
            ) {
              this.yzs = "您已注册,正在前往下载地址";
              this.ts = true;
              window.setTimeout(() => {
                window.location.href =
                  "http://hcurrency.1818pay.cn/down/appdown.html?institutionId=" +
                  self.InstitutionId +
                  "&appId=" +
                  self.appid;
              }, 1500);
            } else if (res.data.respCode == "0001") {
              this.yzs = res.data.respDesc;
              self.ts = true;
            } else {
              this.yzs = res.data.respDesc;
              self.ts = true;
            }
          })
          .catch(erro => {
            self.yzs = "请求错误,请联系技术人员";
            self.ts = true;
          });
      } 
      else {
        this.isdis = true;
      }
    },
    sentget() {
      var self = this;
      var myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (!myreg.test(this.tel)) {
        Dialog
          .alert({
            message: "请输入正确的手机号"
          })
          .then(() => {
            return;
          });
      } else if (this.yzmno.length != 6) {
       Dialog
          .alert({
            message: "请输入正确的验证码"
          })
          .then(() => {
            return;
          });
      } else {
        this.isgetdis = true;
        // let postDatasms = qs.stringify({
        //   identifying:this.yzmno,
        //   merMp:this.tel,
        //   merChantId:this.merChantId,
        //   appId:this.appid
        // })
        axios
          .get(this.host + "MerChants/login.shtml",{
            params:{
              identifying:this.yzmno,
              merMp:this.tel,
              merChantId:this.merChantId,
              appId:this.appid
            }
          })
          .then(res => {
            if (res.data.respCode != "0000") {
              this.yzs = res.data.respDesc;
              this.ts = true;
              this.isgetdis = false;
              return;
            } else {
              // let postDataSetMerChant = qs.stringify({
              //   merMp:this.tel,
              //   merChantId:this.merChantId,
              //   appId:this.appid
              // })
              axios
                .get(this.host + "MerChants/setMerChantsForHtml.shtml",{
                  params:{
                    merMp:this.tjtel,
                    merChantId:this.merChantId,
                    appId:this.appid
                  }
                })
                .then(res => {
                  if (res.data.respCode == "0000") {
                    this.tsok = true;
                    window.setTimeout(() => {
                      window.location.href =
                        "http://hcurrency.1818pay.cn/down/appdown.html?institutionId=" +
                        this.InstitutionId +
                        "&appId=" +
                        this.appid;
                    }, 1500);
                  } else {
                    self.yzs = res.data.respDesc;
                    self.ts = true;
                  }
                });
            }
          });
      }
    }
  }
};
</script>